.list {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

.description {
    width: 140px;
}

.item {
    list-style-type: none;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
        0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}